<template lang="pug">
  .registerBox
    backarrow.ml-5
    .d-flex.justify-content-center.align-items-center.flex-column
      .enterBox.w-75
        p.title.mb-0 註冊
      .dotBox.d-flex.justify-content-center.w-75.mt-4
        .dot.active
        .dot.mx-3(:class="{'active' : step === 'two' || step ==='three'}")
        .dot(:class="{'active' : step ==='three'}")
      .stepBox.w-75.mt-5
        .step(v-if="step === 'one'") Step1 驗證信箱
        .step(v-if="step === 'two'") Step2 發送驗證信件
        .step(v-if="step === 'two'") 系統已發送驗證信件至您指定的信箱，驗證完後請按繼續
        .step(v-if="step === 'three'") 設定密碼
      .inputBox.w-75.d-flex.flex-column.align-items-start(v-if="step === 'one'")
        span.text-danger 信箱將作為您的帳號；因此系統將發送驗證信箱。
        input.form-control(type="text")
        span.title 驗證信箱
        span.placeholder 常用信箱
      .d-flex.w-75.justify-content-center.align-items-end
        .inputBox.d-flex.flex-column.align-items-start(v-if="step === 'two'")
          span.text-danger 信箱將作為您的帳號；因此系統將發送驗證信箱。
          input.form-control.sent(type="text" disabled)
          span.title.text-secondary 驗證信箱
          span.placeholder abc@email.com
        button.reSent(v-if="step === 'two'") 重新發送驗證信(59s)
      .setPasswordBox.w-75.d-flex.flex-column.align-items-start(v-if="step === 'three'")
        input.form-control(type="password")
        span.title 設定新密碼
        span.placeholder 長度為6-20個字元
      .setPasswordBox.w-75.d-flex.flex-column.align-items-start(v-if="step === 'three'")
        input.form-control(type="password")
        span.title 再輸入一次密碼
        span.placeholder 長度為6-20個字元
      a.sentVerify.d-flex.align-items-center.justify-content-center(v-if="step === 'one'" @click="step = 'two'") 傳送驗證
      a.sentVerify.d-flex.align-items-center.justify-content-center(v-if="step === 'two'" @click="step = 'three'") 繼續
      a.sentVerify.d-flex.align-items-center.justify-content-center.mt-4(v-if="step === 'three'" @click="registerDone") 完成
      .otherRegisterBox.w-75
        otherRegister
    .accomplishedModal(v-if="showModal")
      p.mb-0.text-light 設定完成！
      p.mb-0.text-light 請重新登入。


</template>

<script>
import backarrow from '../backarrow.vue';
import otherRegister from './otherRegister.vue';

export default {
  components: {
    backarrow,
    otherRegister,
  },
  data() {
    return {
      step: 'one',
      showModal: false,
    };
  },
  methods: {
    async registerDone() {
      const vm = this;
      vm.showModal = true;
      await vm.changeShowModal().then(() => {
        vm.$router.push('/loginEnterBox');
      });
    },
    changeShowModal() {
      const vm = this;
      return new Promise((resolve) => {
        setTimeout(() => {
          vm.showModal = false;
          resolve('done');
        }, 3000);
      });
    },
  },
};
</script>

